.App {
    text-align: center;
}

.App-logo {
    max-width: 200px;
    pointer-events: none;
    border-radius: 20px;
}

.App-header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

p {
    max-width: 300px;
}

.speech-bubble {
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url("./assets/speech-bubble.png");
    position: absolute;
    left: 90%;
    top: 0%;
    width: 100px;
    height: 100px;
    background-size: contain;
}
